body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  scroll-behavior: smooth;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;

}

:root {
  --secondary-color: #3e6e6e;
}

.wrapper{
  display: block;
  margin: auto;
  max-width: 1440px;
}

.blue {
  color: aqua;
}

body {
  box-sizing: border-box;
  max-width: 100%;
  /* overflow-x: hidden; */
  background-color: rgb(27, 23, 23) !important;
  color: rgb(250, 250, 250) !important;
  font-family: 'Roboto', sans-serif;
  overflow-x:hidden;


}

header {
  max-width: 100%;
  min-height: 900px;
  background-color: rgb(0, 0, 0);
  background-image: url(./logo/4fa0df8e-8469-438b-b7ea-3c35d0ab5084.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  color: white;
}

.logo {
  /* padding: 18px; */
  font-size: 1.5rem;
}

#chackbox{
  display:none;
}

.menu-btn{
  width:25px;
  filter: invert(1);
  margin:0px 8px;
}

.nav {
  margin: auto;
  max-width: 90vw;
  /* min-height:calc(100vh - 90vh); */
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--secondary-color);
  color: azure;
  height: 80px;
  padding: 0 8px;
  box-sizing: border-box;
}

.menu {
  display: block;
}

.menu li {
  display: inline;
  padding: 5px 8px;
}

.menu li a {
  text-decoration: none;
  color: azure;
}

.toggle-btn a {
  text-decoration: none;
  color: aliceblue;
}

#checkbox {
  display: none;
}

#check-btn {
  display: none;
}

.slider {
  margin: auto;
  max-width: 90vw;
  max-height:800px!important;
  min-height: 600px;
  position: relative;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.main-head {
  max-width: 50%;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 2rem 0;

}
.profilepic-container{
  max-width: 50%;
  box-sizing: border-box;
  padding: 8%;
}

.profile-pic {
  border-bottom: 5px dotted aqua;
  border-radius: 50%;
}

.profile-pic img {
  /* padding:3%; */
  max-width: 100%;
  filter: drop-shadow(1px 1px 1px rgba(49, 49, 49, 0.123));
}

.display-text-1 {
  font-size: 3rem;
  line-height: 4rem;
  text-wrap: nowrap;
}

.display-text-2 {
  font-size: 1.5em;
  line-height: 1.2em;
}

.head-card {
  text-align: center;
  width: 90%;
  padding: 5px 8px;
  border-radius: 5px;
  margin: 25px auto;
  display: block;
  color:aqua;
  background-color: rgba(252, 245, 245, 0.301);
  box-shadow: -4px -1px 8px -2px #383838;
}
.sub-head-card{
  text-align: center;
  width: max-content;
  padding: 5px 8px;
  border-radius: 5px;
  margin: 10px auto;
  display: block;
  background-color: rgba(252, 245, 245, 0.301);
  box-shadow: -4px -1px 8px -2px #383838;
  
}

.about {
  max-width: 80vw;
  margin: 50px auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;

}

.container {
  width: 50%;
  position: relative;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  text-align: center;

}
.wrapper-style{
  box-sizing: border-box;
  padding: 20px 20px 50px 20px;
  background-color: var(--secondary-color);
  /* border:1px solid #383838; */
  border-radius:0 0 25% 25%;
  border-bottom: 5px solid white;

}
.wrapper-box{
  position: relative;
  top: -150px;
  width: 90%;
  min-height: 350px;
  margin:auto;
  /* box-sizing: border-box;
  padding: 20px 20px 50px 20px;
  background-color: var(--secondary-color);
  border:1px solid #383838;
  border-radius:0 0 25% 25%; */
}
.wrapper-box::after{
  top:150px;
}
.ab_menu_tp{
  padding: 5px 0;
  margin: 5px auto;
  border-radius: 8px;
  max-width: 750px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: rgb(49, 49, 49);
}
.ab_menu_tp li{
  display: inline;
  padding:5px 3px;
}

.ab_menu_tp li:hover{
  color:aqua
}

/* .ab_menu_tp li:active{
  border-bottom: 5px solid aqua;
} */

.about-cards {
  display: block;
  margin: 10px auto;
  padding: 10px;
  max-width: 750px;
  border-radius: 25px;
  background-color: rgb(49, 49, 49);
  color: white;
  box-sizing: border-box;

}

.cards {
  position: relative;
  width: 80%;
  margin: 1rem 0;
  padding: 25px;
  border-radius: 25px;
  background-color: rgb(49, 49, 49);
  color: white;
  box-sizing: border-box;

}

.h4 {
  margin: 18px 0;
}

.h3 {
  margin: 20px 0;
}

.h2 {
  margin: 22px 0;
}
.text-area>.para{
  text-align: center;
}

.para {
  margin: 5px 0;
  font-weight: 100;
}
.about-parent{
  background-color: rgb(68, 68, 68);
}

.about-me {
  position:relative;
  top:-90px;
  margin:auto;
  padding: 0 0 50px 0;
  width: 90%;
  display: flex;
  flex-direction: row;
  /* flex-wrap: wrap; */
  align-items: center;
  justify-content: space-between;
}

.my-photo {
  width: 50%;
}

.my-photo img {
  border-radius: 50%;
  border-bottom: 5px dotted aqua;
  display: block;
  margin: 25px auto;
  max-width: 60%;
}

.text-area {
  width: 50%;
  padding: 2%;
  text-align: center;
  display: flex;
  flex-direction: column;

}

.quote {
  color: aqua !important;
}
.resume-container{
  background-color: var(--secondary-color);
  margin: auto;
  width: 90%;
  border: none;
  box-sizing: border-box;
  /* border-radius: 35% 35% 0 0; */
  /* padding: 50px 20px 35px 20px; */
  text-align: center;
  border-top: 5px solid white;
  /* box-sizing: border-box; */
  border-radius: 30% 30% 0 0;
  padding: 55px 20px 35px 20px;
 
}
.btn-1{
  border:none;
  padding:10px 16px;
  border-radius: 8px;
  background-color: #ffffff;
  color: rgb(8, 8, 8);
  margin: 5px auto;
  position:relative;
  display: inline-block;
  font-size: 1.5rem;
  font-weight: 400;
  /* box-shadow: -4px -1px 8px -2px #383838; */
  transition: ease-in 0.5s;
}
.btn-1:hover{
  background-color: aqua;
  color:#383838;
  font-weight: 400;
}

.interest{
  max-width:80%;
  position:relative;
  margin:1rem auto;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;

}

.interest-card{
  width: 150px; 
  height: 125px;
  margin: 4px;
  padding:.5rem;
  background-color: #525252;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}

.interest-card img{
  width:100px;
}

.techno{
  max-width:80%;
  position:relative;
  margin:1rem auto;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.techno-card{
  /* width: 150px; */
  /* height: 125px; */
  margin: 4px;
  padding:.5rem;
  background-color: #525252;
  border-radius: 15px;
  display: flex;
  /* flex-direction: column; */
  align-items: center;
  justify-content: center;


}
.techno-img{
  width:45px;
  height:45px;
}

.techno-img img{
  width:100%;
}


.contact-title-container{
  background-color: var(--secondary-color);
  width: 90%;
  margin:auto;

}

.form{
  background-color: var(--secondary-color);
  width:90%;
  margin:auto;
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.form-element{
  margin:2rem 0;

}
.form-element label{
  display: block;
 
}
.textfield{
  display:block;
  position: relative;
  border:none;
  border-radius: 3px;
  background-color: #383838;
  color: #ffffff;
  margin:1% 0;
  width:60%;
  height:2rem;
  font-size: 1.5rem;

}
.textbox{
  display: block;
  position: relative;
  width:60%;
  height: 6rem;
  font-size: 1.5rem;
  border:none;
  border-radius: 3px;
  background-color: #383838;
  color: #ffffff;
  margin:1% 0;
}

.form-label{
  font-size: 1.5rem;
}

.form-btn{
  border:none;
  padding:10px 16px;
  border-radius: 8px;
  background-color: #383838;
  color: aqua;
  margin: 5px auto;
  position:relative;
  display: block;
  font-weight: 400;
  /* box-shadow: -4px -1px 8px -2px #383838; */
  transition: ease-in 0.5s;
  /* display: block;
  position:relative;
  background-color: aqua;
  color: #ffffff;
  text-wrap: nowrap;
  margin:1rem 0;
  padding:.6rem 1rem;
  
  border-bottom:5px solid rgba(255, 255, 255, 0.562);
  font-size: 1.5rem;
  font-weight: 600;
  border-radius: 3px; */

}
.form-btn:hover{
  background-color: rgb(0, 81, 255);
}

.ft-bg{
  border:none;
  max-width: 90%;
  margin:auto;
  background-color: var(--secondary-color);
}

.contactstyle{
  border-bottom: 5px solid white;
}

.contact-details{
  width:90%;
  margin:auto;
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.ct-dtl-h4{
  font-size: 1.5rem;
  font-weight: 500;
  margin:1rem 0;
}
.link{
  color:aqua;
  font-size: 1.5rem;
}

address{
  font-size: 1.2rem;
  font-weight: 500;
}

.head-h2{
  font-weight: 300;
  font-size:20px;
}

.footer {
  margin-top:50px;
}

.fContainer {
  display: flex;
  flex-wrap: wrap;
  align-items:center;
  justify-content:space-between;
  padding:5px;
} 
.fcontent p{
  font-size: 12px;
  text-align: center;
}  

/* .container{
  max-width:50%;
  display:flex;
  flex-direction: column;
  justify-content:flex-start;
  text-align:center;

} */



/* --------mobile view--------- */

@media screen and (max-width:425px) {
  .wrapper-box{
    width: 100%;
  }

  .resume-container{
    width:100%;
  }

  .ft-bg{
    max-width:100%;
  }
}

@media screen and (max-width:768px) {
  .menu {
      top: 60px;
      left: -1000%;
      width: 90vw;
      height:100vh;
      z-index: 100;
      position: absolute;
      display: block;
      background-color: rgba(0, 0, 0, 0.9);
      border-bottom-right-radius: 80px;
      padding-top:1rem;
      padding-bottom: 25px;
      transition: ease-in-out 0.2s;
  }

  /* .menu-open{
  top:60px;
 }
 .menu-close{
 top:1000px;
 } */
  .menu li {
      display: block;
      text-align: center;
      padding: 15px;
  }

  .menu li {
      transition: ease-in-out 0.5s;
  }

  .menu li:hover {
      transform: scale(1.2);
  }

  .menu li a:hover {
      color: aqua;
  }

  #check-btn {
      display: block;
  }

  #checkbox:checked~ul {
      left: -12%;
  }

  .slider {
      flex-direction: column-reverse;
  }

  .main-head {
      max-width: 100%;
  }

  .profilepic-container {
    max-width: 80%;
    box-sizing: border-box;
    padding: 0%;
  }

  .profile-pic {
      max-width: 100%;
  }

  .display-text-1 {
      font-size: 2rem;
      line-height: 3rem;
      text-wrap: nowrap;
  }

  .display-text-2 {
      font-size: 1.5em;
      line-height: 1em;
  }

  .about {
      max-width: 95vw;
  }

  .cards {
      position: relative;
      width: 90%;
      
  }

  .h4 {
      font-size: 16px;
      margin: 14px 0;
  }

  .h3 {
      font-size: 18px;
      margin: 14px 0;
  }

  .h2 {
      font-size: 20px;
      margin: 18px 0;
  }

  .para {
      font-size: 16px;
      margin: 5px 0;
      font-weight: 100;
  }

  .link{
    font-size: 1rem;
  }

  .textfield,.textbox{
      width:100%;
  }

  .form,.contact-details{
      width:100%;
  }

  .fContainer{
    flex-direction: column;
  }
}

@media screen and (max-width:680px) {
  .about {
      max-width: 100vw;
      flex-direction: column;
  }

  .container {
      width: 100%;
  }

  .about-me {
      padding: 50px 0;
      width: 100%;
      display: flex;
      flex-direction: column-reverse;
      align-items: center;
      justify-content: space-between;
  }

  .my-photo {
      width: 100%;

  }

  .my-photo img {
      border-radius: 50%;
      border-bottom: 5px dotted aqua;
      margin: 25px auto;
      display: block;
      max-width: 60%;
  }

  .text-area {
      width: 80%;
  }
}
/* --------------modes--------------------------------- */
